<template lang="html">
  <transition name="fade">
    <div class="main" v-if="show">
      
      <div @click.stop="close"></div>
    </div>
  </transition>
</template>

<script lang="babel">
  export default {
    name: 'Datetime',
    data () {
      return {
        datetime: ''
      }
    },
    methods: {
      close: function () {
        this.$store.commit('TIME_CLOSE', {
          ctrl: false,
          time: '1230 ?'
        });
      }
    },
    computed: {
      show () {
        return this.$store.state.date.ctrl
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .flexItem(@val,@width:0) {
    box-flex: @val;
    -webkit-box-flex: @val;
    -webkit-flex: @val;
    -ms-flex: @val;
    flex: @val;
    width: @width;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }

  .fade-enter, .fade-leave-active {
    opacity: 0
  }

  .main {
    width: 100%;
    overflow: hidden;
    .flexItem(1, 100%);
    background-color: #EEE;
  }
</style>



